<template>
  <div class="container">
    <div v-show="!show">
      <form>
        <div class="form-group">
          <label for="exampleInputDNI1">DNI</label>
          <input v-model="dniForm" type="text" class="form-control" id="exampleInputDNI1" aria-describedby="DNI"
                 placeholder="DNI">
        </div>
        <div class="form-group">
          <label for="exampleInputPassword1">Contraseña</label>
          <input v-model="passForm" type="password" class="form-control" id="exampleInputPassword1"
                 placeholder="Contraseña">
        </div>
        <br>
        <button @click="logUser(dniForm, passForm)" type="button" class="btn btn-primary">Log in</button>
      </form>
    </div>
    <div v-show="show">
      <div class="row">
        <div class="col-12">
          <div v-show="rol==='Admin'">
            <h1>Información del usuario</h1>
            <div id="info">
              <div v-if="userCheck!==null">
                <p>{{ this.userCheck.name }}</p>
                <p>{{ this.userCheck.dni }}</p>
                <p>{{ this.userCheck.id }}</p>
                <p>{{ this.userCheck.vaccine }}</p>
                <p>{{ this.userCheck.centerVaccine }}</p>
                <p>{{ this.userCheck.dateVaccine }}</p>
                <p>{{ this.userCheck.sexo }}</p>
                <p>{{ this.userCheck.isVacunado }}</p>
              </div>
            </div>
          </div>
          <div v-show="rol==='Control'"></div>
          <div v-show="rol==='Sanitario'"></div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>

import firebase from "firebase";

export default {
  name: 'checkUser',
  data() {
    return {
      key: '',
      dniForm: '',
      passForm: '',
      arrayUsers: [],
      show: false,
      userCheck: null,
      rol: '',
      userControl: {},
      localLog: []
    }
  },
  props: {},
  methods: {
    loadUsers: function (users) {
      this.arrayUsers = [];
      for (let key in users) {
        this.arrayUsers.push({
          name: users[key].name,
          password: users[key].password,
          rol: users[key].rol,
          dni: users[key].dni,
          id: users[key].id,
          vaccine: users[key].vaccine,
          centerVaccine: users[key].centerVaccine,
          dateVaccine: users[key].dateVaccine,
          sexo: users[key].sexo,
          isVacunado: users[key].isVacunado
        })
      }
      this.autoLogger();
    },
    logUser(user, pass) {
      //Esta logeado
      if (this.localLog === null) {
        this.dniForm = user;
        this.passForm = pass;
        for (user in this.arrayUsers) {
          if (this.dniForm === this.arrayUsers[user].dni && this.passForm === this.arrayUsers[user].password) {
            if (this.arrayUsers[user].rol === 'Admin' || this.arrayUsers[user].rol === 'Sanitary' || this.arrayUsers[user].rol === 'Control') {
              this.check(this.arrayUsers[user].rol);
              this.userControl = {
                dni: this.dniForm,
                pass: this.passForm,
                rol: this.arrayUsers[user].rol
              };
              localStorage.setItem('userControl', JSON.stringify(this.userControl));
            }
          }
        }
      }
    },
    //Esta previamente logeado
    autoLogger() {
      this.key = this.$route.params.id
      let temp = localStorage.getItem('userControl')
      this.localLog = JSON.parse(temp);
      if (this.localLog !== null) {
        for (let user in this.arrayUsers) {
          if (this.localLog.dni === this.arrayUsers[user].dni && this.localLog.pass === this.arrayUsers[user].password) {
            if (this.localLog.rol === 'Admin' || this.localLog.rol === 'Sanitary' || this.localLog.rol === 'Control') {
              this.check(this.arrayUsers[user].rol);
            }
          } else this.show = false;
        }
      }
    },
    check(rol) {
      for (let user in this.arrayUsers) {
        if (this.key === this.arrayUsers[user].id) {
          this.userCheck = {
            name: this.arrayUsers[user].name,
            dni: this.arrayUsers[user].dni,
            id: this.arrayUsers[user].id,
            vaccine: this.arrayUsers[user].vaccine,
            centerVaccine: this.arrayUsers[user].centerVaccine,
            dateVaccine: this.arrayUsers[user].dateVaccine,
            sexo: this.arrayUsers[user].sexo,
            isVacunado: this.arrayUsers[user].isVacunado
          }
        }
      }
      this.rol = rol;
      this.show = true;
    }
  },
  mounted() {
    this.show = false;
    this.key = '';
    firebase.database().ref('users/').on('value', snapshots => this.loadUsers(snapshots.val()));
  },
}
</script>

