<template>
  <div class="container">
    <div class="card" style="width: 18rem;">
      <vue-qrcode  :key="componentKey" :value="url"/>
      <div class="card-body">
        <h5 class="card-title">{{ this.infoUser.name }}</h5>
      
        <p class="card-text"></p>

      </div>
    </div>
  </div>
</template>

<script>
import VueQrcode from 'vue-qrcode'
import firebase from "firebase";

export default {
  name: 'UserComp',
  data() {
    return {
      infoUser: '',
      url: '',
      componentKey: 0,
      localLog:''
    }
  },
  components: {
    VueQrcode,
  },
  props: {},
  methods: {
       reload(id) {
          let temp = localStorage.getItem('userLogged')
          this.localLog = JSON.parse(temp);
          firebase
            .database()
            .ref("users/" + this.localLog.dni)
            .set({
              name: this.localLog.name,
              password: this.localLog.password,
              dni: this.localLog.dni,
              id: id,
              vaccine: this.localLog.vaccine,
              centerVaccine:this.localLog.centerVaccine,
              dateVaccine: this.localLog.dateVaccine,
              rol: this.localLog.rol,
              sexo: this.localLog.sexo,
              isVacunado: this.localLog.isVacunado,
            });
          let userObj = {
            name: this.localLog.name,
            password: this.localLog.password,
            dni: this.localLog.dni,
            id: id,
            vaccine: this.localLog.vaccine,
            centerVaccine: this.localLog.centerVaccine,
            dateVaccine: this.localLog.dateVaccine,
            rol: this.localLog.rol,
            sexo: this.localLog.sexo,
            isVacunado: this.localLog.isVacunado,
          };
          localStorage.setItem("userLogged", JSON.stringify(userObj));
    },
    forceRerender() {
      this.componentKey += 1;
      let tempKey = this.getRandomKey();
      this.url = 'https://sars-pass.netlify.app/#/check/' + tempKey;
      this.reload(tempKey);
    },
    getRandomKey() {
      const randomChars =
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      let result = "";
      for (let i = 0; i < 25; i++) {
        result += randomChars.charAt(
          Math.floor(Math.random() * randomChars.length)
        );
      }
      return result;
    }
  },
  mounted() {
    this.infoUser = JSON.parse(localStorage.getItem('userLogged'));
    this.url = 'https://sars-pass.netlify.app/#/check/' + this.infoUser.id;
    setInterval(this.forceRerender, 30000 );
  },
}
</script>

